.content {
  width: 100%;
}
.cards {
  padding: 0 2em;
}
.card {
  width: 30%;
  float: left;
  margin: 5px 10px;
  padding: 10px;
}
.card-body {
  font-size: 18px;
  text-align: justify;

  min-height: 150px;
}
.card .divisor {
  height: 2px;
  background-color: #80081ef6;
}
.tags {
  text-align: left;
}
.tags div {
  font-size: 1rem;
  border: 2px solid #42040f;
  background: #42040f;
  padding: 2px 5px;
  border-radius: 10px;
  margin: 2px 5px;
  width: fit-content;
  float: left;
}
